
nav {
  font-size: 14px;
  padding: 15px;
}
nav a {
  display: block;
  position: relative;
  padding: 10px 0 10px 10px;
  color: #525558;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
nav a.v-link-active,
nav a:hover {
  color: #42b983;
}
nav div {
  font-size: 16px;
  margin: 8px 0;
}


